.wrapper {
    display: flex;
    align-items: center;
    padding: 9px 16px;
    cursor: pointer;
    text-decoration: none;
}

.avatar {
    width: 40px;
    height: 40px;
    margin-right: 8px;
    border-radius: 50%;
    object-fit: cover;
}

.info {
    flex: 1;
}

.name {
    font-size: 1.6rem;
    line-height: 22px;
    font-weight: 500;
    color: rgba(22, 24, 35, 1);
}

.check {
    font-size: 1.4rem;
    color: rgb(32, 213, 236);
    padding-left: 4px;
}

.username {
    font-size: 1.4rem;
    line-height: 20px;
    color: rgba(22, 24, 35, 0.5);
    font-weight: 400;
}
